import React, { Fragment } from 'react';

const Home = ({ lang }) => {
  return (
    <Fragment>
      <h3 className='grey-text text-darken-2'>
        {lang === 'en' ? (
          <div className='animated fadeInRightBig'>
            <p>Hello, I'm Alex.</p>
            <p>I'm web developer from Minsk, Belarus.</p>
          </div>
        ) : (
          <div className='animated fadeInRightBig'>
            <p>Привет, меня зовут Александр.</p>
            <p>Я веб-разработчик из Минска, Беларусь.</p>
          </div>
        )}
        <div className='animated fadeInUpBig'>
          <a
            href='#projects'
            // onClick={() => switchPage(true)}
            className='waves-effect waves-light btn-large center'
          >
            <i className='material-icons right'>trending_flat</i>
            {lang === 'en' ? 'my projects' : 'мои работы'}
          </a>
        </div>
      </h3>
    </Fragment>
  );
};

export default Home;
